<script>
    export default {
        globalData: {
            userInfo: null,
            systemInfo: null,
            $spaceId: 'mp-d0c06b27-ec33-40fe-b28b-337811bd2f29', // UniCloud阿里云空间ID
            $isDevMode: false // 是否使用模拟数据
        },
        onLaunch: function() {
            console.log('App Launch')

            // 测试云函数连接
            this.testCloudConnection()

            // 获取设备信息
            this.getSystemInfo()

            // 获取用户登录状态
            this.checkLoginStatus()
        },
        onShow: function() {
            console.log('App Show')
        },
        onHide: function() {
            console.log('App Hide')
        },
        methods: {
            // 获取系统信息
            getSystemInfo() {
                try {
                    const systemInfo = uni.getSystemInfoSync()
                    this.globalData.systemInfo = systemInfo
                    console.log('系统信息:', systemInfo)
                } catch (e) {
                    console.error('获取系统信息失败:', e)
                }
            },

            // 检查登录状态
            checkLoginStatus() {
                try {
                    const userInfo = uni.getStorageSync('userInfo')
                    if (userInfo) {
                        this.globalData.userInfo = JSON.parse(userInfo)
                    }
                } catch (e) {
                    console.error('检查登录状态失败:', e)
                }
            },

            // 测试云函数连接
            testCloudConnection() {
                console.log('正在测试阿里云函数连接...');
                uni.cloud.callFunction({
                    name: 'test',
                    data: {
                        message: '应用启动测试'
                    }
                }).then(res => {
                    console.log('阿里云函数连接测试结果:', res.result);
                    if (res.result && res.result.code === 0) {
                        // 云函数连接成功，继续初始化数据库
                        this.initCloudDatabase();
                    } else {
                        throw new Error(res.result?.message || '测试返回异常结果');
                    }
                }).catch(err => {
                    console.error('阿里云函数连接测试失败:', err);
                    // 连接失败时自动使用模拟数据
                    getApp().globalData.$isDevMode = true;
                });
            },

            // 初始化云数据库
            initCloudDatabase() {
                // 尝试初始化数据库，确保有必要的集合
                uni.cloud.callFunction({
                    name: 'initDatabase'
                }).then(res => {
                    console.log('数据库初始化结果:', res.result);
                    if (res.result && res.result.success) {
                        // 数据库初始化成功，可以进行其他操作
                        this.checkAndImportData();
                    }
                }).catch(err => {
                    console.error('数据库初始化失败，降级使用模拟数据:', err);
                    // 初始化失败时自动使用模拟数据
                    getApp().globalData.$isDevMode = true;
                });
            },

            // 检查并导入数据
            checkAndImportData() {
                // 依次检查各个集合中是否有数据，如果没有则导入
                const collections = ['courses', 'teachers', 'news', 'users', 'bookings'];
                const mockData = require('./pages/admin/mock-data.js');

                // 依次导入数据
                collections.forEach(collection => {
                    uni.cloud.callFunction({
                        name: 'importData',
                        data: {
                            collection,
                            data: mockData[collection] || [],
                            clear: false // 不清空现有数据
                        }
                    }).then(res => {
                        console.log(`导入${collection}数据结果:`, res.result);
                    }).catch(err => {
                        console.error(`导入${collection}数据失败:`, err);
                    });
                });
            }
        }
    }
</script>

